<template>
    <div class="h-key-lesson">
      <div class="lesson-container">
        <div class="lesson-text">
          <h2>Lesson 9: The h Key</h2>
          <p class="instruction">
            {{ isLessonComplete 
              ? 'Lesson complete! Choose your next step.' 
              : 'Move your right index finger one key to the left from j, and practice pressing the h key.' 
            }}
          </p>
          <p class="finger-movement-tip">
            <span class="movement-arrow">←</span> 
            Move right index finger left from home position (j) to h
          </p>
          <p class="target-key" :class="{ 'completed': isLessonComplete }">
            {{ isLessonComplete ? '✓ Complete!' : 'Press: h' }}
          </p>
          <p class="counter">Presses: {{ keyPressCount }} / {{ requiredPresses }}</p>
          <p v-if="isLessonComplete" class="success-message">
            Great job! You've completed the lesson!
          </p>
          <div v-if="isLessonComplete" class="button-container">
            <button 
              @click="resetLesson" 
              class="try-again-btn"
            >
              Try Again
            </button>
            <button 
              @click="goToNextLesson" 
              class="next-lesson-btn"
              autofocus
            >
              Next Lesson: g Key
            </button>
          </div>
        </div>
      </div>
      <KeyboardComponent 
        :activeKey="activeKey"
        :targetKey="targetKey"
      />
    </div>
  </template>
  
  <script>
  import { keyboardMixin } from '../../../mixins/keyboardMixin'
  import KeyboardComponent from '../../../components/KeyboardComponent.vue'
  import '@/assets/styles/lessonStyles.css'
  
  export default {
    name: 'HKeyLesson',
    components: {
      KeyboardComponent
    },
    mixins: [keyboardMixin],
    data() {
      return {
        keyPressCount: 0,
        isLessonComplete: false,
        targetKey: 'h',
        requiredPresses: 10,
        activeKey: null
      }
    },
    methods: {
      handleLessonKeyPress(key) {
        if (this.isLessonComplete) return;
  
        if (key === this.targetKey) {
          if (this.keyPressCount < this.requiredPresses) {
            this.keyPressCount++
            if (this.keyPressCount >= this.requiredPresses) {
              this.isLessonComplete = true
              this.activeKey = null
            }
          }
        }
      },
      resetLesson() {
        this.keyPressCount = 0
        this.isLessonComplete = false
        this.activeKey = null
      },
      goToNextLesson() {
        this.$router.push('/lessons/GKeyLesson')
      }
    }
  }
  </script>
  
  <style scoped>
  .h-key-lesson {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }

  .finger-movement-tip {
    background-color: #fff3cd;
    border: 1px solid #ffeeba;
    color: #856404;
    padding: 10px 15px;
    border-radius: 4px;
    margin: 10px 0;
    font-size: 0.9em;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
  }

  .movement-arrow {
    font-size: 1.2em;
    font-weight: bold;
  }
  </style>